<template>
  <div>
    <b-row>
      <block-io-keys></block-io-keys>
      <summary-cards></summary-cards>
    </b-row>

    <b-card no-body>
      <b-card-header header-tag="nav">
        <b-nav card-header fill tabs justified>
          <b-nav-item
            @click.prevent="withdrawalStatus = 'pending'"
            exact
            exact-active-class="active"
            :active="withdrawalStatus === 'pending'"
            >Pending</b-nav-item
          >
          <b-nav-item
            @click.prevent="withdrawalStatus = 'paid'"
            exact
            exact-active-class="active"
            :active="withdrawalStatus === 'paid'"
            >Paid</b-nav-item
          >
        </b-nav>
      </b-card-header>

      <b-card-body>
        <b-row class="mt-3">
          <b-col md="4" class="my-1">
            <b-form-group horizontal label="Filter" class="mb-0">
              <b-input-group>
                <b-form-input v-model="filter" placeholder="Search Username" />
                <b-input-group-append>
                  <b-btn :disabled="!filter" @click="filter = ''">Clear</b-btn>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
          </b-col>

          <b-col md="6" class="my-1">
            <b-form-group horizontal label="Sort" class="mb-0">
              <b-input-group>
                <b-form-select v-model="sortBy" :options="sortOptions">
                </b-form-select>

                <b-form-select
                  :disabled="!sortBy"
                  v-model="sortDesc"
                  slot="append"
                >
                  <option :value="false">Asc</option>
                  <option :value="true">Desc</option>
                </b-form-select>
              </b-input-group>
            </b-form-group>
          </b-col>

          <b-col md="2" class="my-1">
            <b-form-group horizontal label="Per page" class="mb-0">
              <b-form-select
                :value="perPage"
                :options="pageOptions"
                @change="(val) => setPerPage(val)"
              />
            </b-form-group>
          </b-col>

          <template v-if="withdrawalStatus === 'pending'">
            <b-col md="6" sm="6" class="my-2 mt-4">
              <button
                class="btn btn-secondary btn-block"
                @click.prevent="selectALL(items)"
              >
                Select All
              </button>
            </b-col>
          </template>

          <b-col md="6" sm="6" class="my-2 mt-4">
            <button class="btn btn-success btn-block" @click="csvExport(items)">
              Export CSV
            </button>
          </b-col>
        </b-row>

        <small>
          {{ withdrawalSelectedItems.length }}
          Selected Items
        </small>
        <b-table
          :hover="hover"
          :striped="striped"
          :bordered="bordered"
          :small="small"
          :fixed="fixed"
          responsive="sm"
          :items="items"
          :fields="fields"
          :per-page="perPage"
          :filter="filter"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          @filtered="onFiltered"
          class="mt-3"
          id="myTable"
          :busy="loading"
        >
          <template v-slot:cell(option)="data">
            <template v-if="data.item.withdrawal_status === 'pending'">
              <b-form-checkbox
                :id="'checkboxID' + data.item.id"
                @change="selectItem(data.item)"
                :value="'accepted' + data.item.id"
                unchecked-value="not_accepted"
              ></b-form-checkbox>
            </template>
          </template>

          <template v-slot:cell(username)="data">
            <span class="badge badge-primary">{{
              data.item.user.username
            }}</span>
          </template>

          <template v-slot:cell(withdrawal_type)="data">
            <template v-if="data.item.withdrawal_type">
              <span
                class="badge "
                :class="
                  (data.item.withdrawal_type === 'daily' && 'badge-danger') ||
                    (data.item.withdrawal_type === 'comissions' &&
                      'badge-success') ||
                    'badge-warning'
                "
              >
                {{ data.item.withdrawal_type }}
              </span>
            </template>
            <template v-else>
              <span class="badge badge-dark">NO</span>
            </template>
          </template>

          <template v-slot:cell(currency)="data">
            <span class="badge badge-warning">{{
              data.item.currency ? data.item.currency.name : ""
            }}</span>
          </template>

          <template v-slot:cell(total)="data">{{ data.item.value }}</template>

          <template v-slot:cell(to_pay)="data">
            <strong>{{ data.item.settle }}</strong>
          </template>

          <template v-slot:cell(fees)="data">{{ data.item.fees }}</template>

          <template v-slot:cell(status)="data">
            <template v-if="data.item.withdrawal_status === 'paid'">
              <span class="badge badge-success">{{
                data.item.withdrawal_status
              }}</span>
            </template>
            <template v-else-if="data.item.withdrawal_status === 'pending'">
              <span class="badge badge-warning">{{
                data.item.withdrawal_status
              }}</span>
            </template>
            <template v-else>
              <span class="badge badge-danger">{{
                data.item.withdrawal_status
              }}</span>
            </template>
          </template>

          <template v-slot:cell(action)="data">
            <button
              v-if="data.item.withdrawal_status === 'paid'"
              class="btn btn-sm btn-primary ml-1"
              @click="openManualPaymentModal(data.item)"
            >
              Details
              <i class="fa fa-plus" />
            </button>

            <button
              v-else
              class="btn btn-sm btn-dark ml-1"
              @click="openManualPaymentModal(data.item)"
            >
              Aprove
              <i class="fa fa-check" />
            </button>

            <button
              v-if="data.item.withdrawal_status === 'pending'"
              @click.prevent="deleteWithdrawal(data.item)"
              class="btn btn-sm btn-danger ml-1"
            >
              Delete
              <i class="fa fa-times" />
            </button>
          </template>
        </b-table>

        <nav>
          <paginate
            :page-count="pageCount"
            :page-range="3"
            :click-handler="nextPageCall"
            :prev-text="'Prev'"
            :next-text="'Next'"
            :container-class="'pagination b-pagination pagination-md'"
            :prev-class="'page-item'"
            :prev-link-class="'page-link'"
            :next-class="'page-item'"
            :next-link-class="'page-link'"
            :page-class="'page-item'"
            :page-link-class="'page-link'"
          ></paginate>
        </nav>
      </b-card-body>
    </b-card>

    <ManualPayment
      v-if="$store.getters['withdrawals/getManualPaymentModalOpen']"
    ></ManualPayment>
    <MassivePayment
      v-if="$store.getters['withdrawals/getMassivePaymentModalEnabled']"
    ></MassivePayment>
  </div>
</template>

<script>
import MassivePayment from "./MassivePayment";
import ManualPayment from "./ManualPayment";
import BlockIoKeys from "./BlockIoKeys";
import SummaryCards from "./SummaryCards";
import { MathMixin, NavCommon } from "@/mixins/";

import _ from "lodash";
import * as table2csv from "table2csv";
import { mapGetters, mapActions } from "vuex";
import Paginate from "vuejs-paginate";

export default {
  name: "withdrawal-list",
  mixins: [MathMixin, NavCommon],
  props: {
    server: { type: String },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: true
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Paginate,
    ManualPayment,
    MassivePayment,
    BlockIoKeys,
    SummaryCards
  },
  data() {
    return {
      fields: [
        { key: "id", sortable: true, label: "ID" },
        {
          key: "option",
          label: "",
          thClass: "text-center",
          tdClass: "text-center"
        },
        { key: "withdrawal_type", label: "Type" },
        { key: "username", label: "Username", sortable: false },
        { key: "currency", label: "Currency" },
        { key: "total", label: "Sub-Total", sortable: true },
        { key: "fees", label: "Fees", sortable: true },
        { key: "to_pay", label: "Total", sortable: true },
        { key: "status", label: "Status" },
        {
          key: "created_at",
          label: "Created At",
          thClass: "text-right",
          tdClass: "text-right"
        },
        {
          key: "action",
          label: "Action",
          thClass: "text-right",
          tdClass: "text-right"
        }
      ],
      totalRows: 0,
      filter: null,
      pageOptions: [1, 2, 3, 5, 10, 15, 20, 25, 30, 40, 50, 100, 500, 1000],
      sortBy: "id",
      sortDesc: true,
      withdrawalStatus: "pending"
    };
  },
  computed: {
    ...mapGetters("withdrawals", {
      items: "getWithdrawalsList",
      loading: "isLoading",
      pageCount: "getPageCount",
      massivePaymentModalEnabled: "getMassivePaymentModalEnabled",
      withdrawalSelectedItems: "getWithdrawalSelectedItems",
      massivePaymentCache: "getMassivePaymentCache",
      pageNum: "getPageNum",
      perPage: "getPerPage"
    }),
    sortOptions() {
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => {
          return { text: f.label, value: f.key };
        });
    }
  },
  beforeDestroy() {
    this.reset();
  },
  watch: {
    withdrawalStatus() {
      this.fetchSummary({
        withdrawalStatus: this.withdrawalStatus
      });
      this.listWithdrawals({
        withdrawalStatus: this.withdrawalStatus
      });
    },
    massivePaymentModalEnabled(newVal) {
      if (!newVal) this.selectALL(this.massivePaymentCache);
    }
  },
  created() {
    this.reset();
    this.fetchSummary({
      withdrawalStatus: this.withdrawalStatus
    });
    this.listWithdrawals({
      withdrawalStatus: this.withdrawalStatus
    });
  },
  methods: {
    ...mapActions("withdrawals", {
      reset: "reset",
      listWithdrawals: "fetchList",
      fetchSummary: "fetchSummary"
    }),
    selectALL(items) {
      items.forEach((x) => {
        let element = document.querySelector("#checkboxID" + x.id);
        element.click();
      });
    },
    selectItem(object) {
      let index = this.withdrawalSelectedItems.indexOf(object);

      if (index >= 0) {
        this.withdrawalSelectedItems.splice(index, 1);
      } else if (
        this.withdrawalSelectedItems
          .map((x) => x.wallet)
          .includes(object.wallet)
      ) {
        this.$toasted.show("Wallet already selected", {
          position: "top-right",
          duration: 5000,
          closeOnSwipe: true
        });
      } else {
        this.$store.commit("withdrawals/setWithdrawalSelectedItems", object);
      }
    },
    openManualPaymentModal(item) {
      this.$store.dispatch("withdrawals/selectedWithdrawal", item);
    },
    deleteWithdrawal(item) {
      this.$store.dispatch("withdrawals/deleteWithdrawal", item);
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.$store.commit("withdrawals/setPageNum", 1);
    },
    setPerPage(val) {
      this.$store.commit("withdrawals/setPerPage", val);
      this.$store.commit("withdrawals/setPageNum", 1);
      this.listWithdrawals({
        withdrawalStatus: this.withdrawalStatus
      });
    },
    nextPageCall(pageNum) {
      this.$store.commit("withdrawals/setPageNum", pageNum);
      this.listWithdrawals({
        withdrawalStatus: this.withdrawalStatus
      });
    },
    csvExport(arrData) {
      $("#myTable").table2csv();
    }
  }
};
</script>
