import { currency } from "@/filters";

import { mapGetters } from "vuex";

export default {
  name: "withdrawals-manual-payment",
  filters: { currency },
  mounted() {
    this.showModal();
  },
  computed: {
    ...mapGetters("withdrawals", {
      item: "getWithdrawalShow",
      loading: "isLoading",
    }),
    isPaid() {
      return this.item.withdrawal_status === "paid";
    },
  },
  methods: {
    showModal() {
      this.$root.$emit("bv::show::modal", "modalManualVariants");
    },
    hideModal() {
      this.$root.$emit("bv::hide::modal", "modalManualVariants");
    },
    closeModal() {
      this.$store.commit("withdrawals/setManualPaymentModalOpen", false);
      this.$store.commit("withdrawals/setWithdrawalsShow", {});
      this.hideModal();
    },
  },
};
