export default {
  namespaced: true,
  name: "blockio-keys",
  data() {
    return {
      apiKey: "",
      apiSecret: "",
    };
  },
  watch: {
    apiKey(newVal) {
      this.$store.commit("withdrawals/setBlockioApi", newVal);
    },
    apiSecret(newVal) {
      this.$store.commit("withdrawals/setBlockioSecret", newVal);
    },
  },
  methods: {
    openModal() {
      this.$store.commit("withdrawals/setMassivePaymentModalEnabled", true);
    },
  },
};
