/**
 * Return value to correct currency style
 *
 * @param value
 * @param format
 * @param symbol
 * @param decimals
 * @returns {*}
 */
export default function(value, format = null, symbol = "", decimals = 2) {
  value = parseFloat(value);
  // let val = (value / 1).toFixed(2).replace(".", ",");
  // return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  // Check value exists
  if (!value) {
    return "-";
  }
  // Set value decimals
  let cleanValue = (value / 1)
    .toFixed(decimals)
    .replace(".", ",")
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  // Use format if set
  if (format) {
    return format.replace("{%}", cleanValue);
  }
  // Fallback style
  return `${symbol}${cleanValue}`;
}
