import _ from "lodash";
import { mapActions, mapGetters } from "vuex";

import { AuthCommon } from "@/mixins/";
import { currency } from "@/filters";

export default {
  name: "withdrawals-massive-payment",
  mixins: [AuthCommon],
  filters: { currency },
  data() {
    return {
      items: [],
      totalPaymentUSD: 0,
    };
  },
  computed: {
    ...mapGetters("withdrawals", {
      loading: "isLoading",
      totalPaymentFees: "getTotalMassivePaymentFees",
      totalPaymentBTC: "getTotalPaymentBTC",
    }),
    totalPaymentFeesPercentage() {
      return (this.totalPaymentFees * 100) / this.totalPaymentBTC;
    },
  },
  mounted() {
    this.showModal();
    this.$store.getters["withdrawals/getWithdrawalSelectedItems"].map(
      (item) => {
        this.calculateCrypto(item);
      }
    );
  },
  watch: {
    items() {
      this.totalPaymentUSD = this.items
        .map((obj) => obj.settle)
        .reduce((a, b) => a + b, 0)
        .toFixed(2);

      if (!this.items.map((obj) => obj.totalCrypto).includes(0)) {
        this.buildPaymentFees({
          items: this.items,
        });
      }
    },
  },
  methods: {
    ...mapActions("withdrawals", {
      createMassivePayment: "createMassivePayment",
      buildPaymentFees: "buildPaymentFees",
    }),
    async confirmWithdraw() {
      await this.checkSession();
      (await this.$store.getters.activeSession) &&
        this.createMassivePayment({
          items: this.items,
        });
    },
    calculateCrypto(item) {
      let total = (
        item.settle / this.$store.getters["withdrawals/getBtcLastPrice"]
      ).toFixed(6);
      item["totalCrypto"] = parseFloat(total);
      this.items.push(item);
    },
    showModal() {
      this.$root.$emit("bv::show::modal", "MassivePaymentModal");
    },
    hideModal() {
      this.$root.$emit("bv::hide::modal", "MassivePaymentModal");
    },
    closeModal() {
      this.$store.commit("withdrawals/setMassivePaymentCache", this.items);
      this.$store.commit("withdrawals/setMassivePaymentModalEnabled", false);
      // this.$store.commit("withdrawals/resetWithdrawalSelectedItems");
      this.$store.dispatch("withdrawals/fetchList", {
        withdrawalStatus: "pending",
      });
      this.hideModal();
    },
  },
};
